// /app/page.js

"use client"

import { useEffect } from 'react'; // useEffect Hook 추가
import { useRouter } from 'next/navigation'; // useRouter Hook 추가
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { LogOut, User, Home, Loader2 } from "lucide-react" // Loader2 아이콘 추가

export default function MainPage() {
  const router = useRouter();

  // 클라이언트 측에서만 localStorage에 접근
  const accessToken = typeof window !== 'undefined' ? localStorage.getItem("accessToken") : null

  // ⭐️ 핵심 로직: 로그인 상태 확인 및 리다이렉트 ⭐️
  useEffect(() => {
    if (accessToken) {
      // accessToken이 존재하면, 즉시 /dashboard로 이동
      router.replace('/dashboard');
      // 이후의 렌더링은 잠시 막고 대시보드로 이동합니다.
    }
    // accessToken이 없으면, 아래의 로그인 필요 UI를 계속 렌더링합니다.
  }, [accessToken, router]);


  const handleLogout = () => {
    // 로컬 저장소 토큰 및 사용자 정보 삭제 (완전한 로그아웃)
    localStorage.removeItem("accessToken")
    localStorage.removeItem("refreshToken")
    localStorage.removeItem("userName") // 이전에 저장했을 수 있으므로 추가
    
    // 로그인 페이지로 리다이렉트 (새로고침을 유발)
    window.location.href = "/login" 
  }

  // ⭐️ accessToken이 있지만 리다이렉션이 아직 완료되지 않았을 때 로딩 UI를 표시합니다. ⭐️
  if (accessToken) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-100">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
        <p className="ml-2 text-gray-700">대시보드로 이동 중...</p>
      </div>
    );
  }

  // ⭐️ accessToken이 없을 때 (로그인 필요 UI) ⭐️
  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 bg-gray-100">
      <div className="w-full max-w-lg p-10 bg-white rounded-xl shadow-2xl space-y-8 text-center">
        <Home className="h-14 w-14 text-primary mx-auto" />
        
        <h1 className="text-4xl font-extrabold text-gray-800">Next Me 서비스 메인</h1>
        
        {/* accessToken이 없으므로 이 블록만 렌더링됩니다. */}
        <div className="space-y-4">
          <p className="text-lg text-red-500 font-medium">
            로그인이 필요합니다.
          </p>
          <Button asChild className="w-full h-12 text-lg">
            <Link href="/login">
              로그인 페이지로 이동
            </Link>
          </Button>
        </div>
        
      </div>
    </div>
  )
}